import { useState, useEffect } from 'react';
import log from './img/log.svg';
import { Nav } from 'react-bootstrap';
import { Link, Route, Switch, useHistory } from 'react-router-dom';
import axios from 'axios';
import './AccessPage.css';


function AccessPage() {
  // let [signType, setSignType] =useState("signIn");
  const [position, setPosition] = useState("student");

  const [stdID, setStdID] = useState("");
  const [name, setName] = useState("");
  const [major, setMajor] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [email, setEmail] = useState("");
  const [certFile, setCertFile] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [checkState, setCheckState] = useState(false);
  const [atIsContain, setAtIsContains] = useState(false);
  // let [majorList, setMajorList] = useState(["   ."]);
  const [majorList, setMajorList] = useState(["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "IT", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "()", "()", "()", "(ICT)", "()", "()", "()", "()", "()", "()", "()", "()", "()", "()", "", "", "", ""]);


  const history = useHistory();


  useEffect(() => {
    if (phoneNumber.length === 10) {
      setPhoneNumber(phoneNumber.replace(/(\d{3})(\d{3})(\d{4})/, '$1-$2-$3'));
    }
    if (phoneNumber.length === 13) {
      setPhoneNumber(phoneNumber.replace(/-/g, '').replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3'));
    }
  }, [phoneNumber]);

  useEffect(() => {
    console.log(email.lastIndexOf("@"));
    if (email.indexOf("@") !== -1) setAtIsContains(true);
    else setAtIsContains(false);


    if (email[email.length - 1] === "@" && atIsContain === false) {
      setEmail(email + "pukyong.ac.kr")
    }
  }, [email]);

  useEffect(() => {
    // https://pkscl.kro.kr/major-list
    //'/major-list'
    axios.get('https://pkscl.kro.kr/major-list')
      .then((payload) => {
        setMajorList([...payload.data.majorList]);
      })
      .catch((error) => {
        alert("   .");
      })

  }, []);


  function reset() {
    setStdID("");
    setName("");
    setMajor("");
    setPassword("");
    setCheckPassword("");
    setEmail("");
    setCertFile("");
  };


  function login() {
    if (email === "" || password === "") {
      return (
        alert("   ")
      )
    }
    else {
      let payload = { "email": email, "password": password };
      // debugger;
      axios.post('/login/' + position, payload)
        .then((payload) => {
          console.log(payload);
          if (payload.data.position === "student") {
            history.push('/main/' + payload.data.major);
          }
          else if (payload.data.position === "president") {
            history.push('/manage/' + payload.data.major);
          }

        })
        .catch((result) => {
          console.log(result.status);
          alert(" .")


        });

    }
  };

  function findPassword() { //
    if (email === "" || stdID === "" || name === "") {
      return (
        alert("  ")
      )
    }
    else {
      let payload = { "email": email, "stdID": stdID, "name": name };
      axios.post('/newpwd/' + position, payload)
        .then((result) => {
          console.log(payload);
          if (window.confirm('    .')) {
            history.push('/');
          }
          else {
            history.push('/newpwd');
          }

        })
        .catch((error) => {
          console.log(payload);
          alert("    .");

        });

    }
  };

  function checkHandler() {
    setCheckState(!checkState); //      ??
    if (checkState === false) {
      setPosition("president");
      console.log(position); //    log  ;;;
    } else {
      setPosition("student");
      console.log(position); //    log  ;;;
    }
  };

  function certEmail() {
    let payload = { "email": email };
    axios.post('/email/' + position, payload)
      .then((result) => {
        alert("   .");
      })
      .catch((error) => {
        console.log(payload);
        alert(error.data.errorMessage);
      });
  }






  return (
    <div className="container">

      <div className="left-panel">
        <div className="content">
          <button className="btn admin" id="sign-in-btn">
            
          </button>
          <h3>PKNU  </h3>
          <p>
                   .
          </p>
        </div>
        <img src={log} className="image" alt="" />
      </div>
      <Switch>
        <Route exact path="/signUp">
          <div className="right-panel">
            <form className="userForm" action={"/signup/" + position} method="post" encType="multipart/form-data" >
              <div id="nav" >
                <Nav fill variant="tabs" defaultActiveKey="link-1">
                  <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={() => { setPosition("student"); reset(); }}></Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick={() => { setPosition("president"); reset(); }}></Nav.Link>
                  </Nav.Item>
                </Nav>
              </div>

              <div className="input-field">
                <i className="fas fa-lock"></i>
                <input onChange={(e) => { setStdID(e.target.value.replace(/[^0-9]/g, '')) }} name="stdID" value={stdID} maxLength="9" placeholder="" type="text" />
              </div>
              <div className="input-field">
                <i className="fas fa-key"></i>
                <input onChange={(e) => { setPassword(e.target.value) }} name="password" value={password} type="password" placeholder="" />
              </div>
              <div className="input-field">
                <i className="fas fa-key"></i>
                <input onChange={(e) => { setCheckPassword(e.target.value) }} value={checkPassword} type="password" placeholder=" " />
              </div>
              <div className="input-field" style={{ fontSize: "80%" }}>
                <i className="fas fa-book-open"></i>
                <select name="major" style={{ border: "0px", background: "#f0f0f0" }} >
                  <option value={-1} selected> </option>
                  {
                    majorList.map((major, i) => {
                      return (<option value={i + 1} key={i} >{major}</option>)
                    })
                  }




                </select>
              </div>
              <div className="input-field">
                <i className="fas fa-user"></i>
                <input onChange={(e) => { setName(e.target.value) }} name="name" value={name} type="text" placeholder="" />
              </div>
              {
                position === "president"
                  ?
                  (<div className="input-field">
                    <i className="fas fa-phone-alt"></i>
                    <input onChange={(e) => { setPhoneNumber(e.target.value) }} maxLength="13" name="phoneNumber" value={phoneNumber} type="text" placeholder="" />
                  </div>
                  )
                  :
                  null

              }

              <div className="input-field">
                <i className="fas fa-envelope"></i>
                <input onChange={(e) => { setEmail(e.target.value) }} name="email" value={email} type="text" placeholder="  @pukyong.ac.kr" />
                <label onClick={certEmail}></label>
              </div>
              <div className="input-field filebox">
                <i className="fas fa-user-graduate"></i>
                <input className='uploadName' placeholder=' ' value={certFile} readOnly />
                <label htmlFor="certFile"></label>
                <input type="file" id='certFile' name="certFile" accept='image/*' onChange={(e) => { setCertFile(e.target.value.split('/').pop().split('\\').pop()) }} />
              </div>



              <div className="submitbox" >
                <button type="submit" className="SignInBtn"></button>
              </div>
            </form>
            <div className='moveSignPage'>
              <button onClick={() => { reset(); history.push('/newpwd') }}> </button><button onClick={() => { reset(); history.push('/'); }}></button>
            </div>
          </div>

        </Route >

        <Route exact path="/">
          <div className="right-panel">
            <form className="userForm">
              <div id="nav" >
                <Nav fill variant="tabs" defaultActiveKey="link-1">
                  <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={() => { setPosition("student"); reset(); }}></Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick={() => { setPosition("president"); reset(); }}></Nav.Link>
                  </Nav.Item>
                </Nav>
              </div>
              <div className="input-field">
                <i className="fas fa-envelope"></i>
                <input onChange={(e) => { setEmail(e.target.value) }} value={email} type="text" placeholder="  @pukyong.ac.kr" />
              </div>
              <div className="input-field">
                <i className="fas fa-key"></i>
                <input onChange={(e) => { setPassword(e.target.value) }} value={password} type="password" placeholder="" />
              </div>

              <div className="submitbox" >
                <button type="button" onClick={() => { login() }} value="Login" className="SignInBtn"></button>
              </div>
            </form>
            <div className='moveSignPage'>
              <button onClick={() => { reset(); history.push('/newpwd') }}> </button><button onClick={() => { reset(); history.push('/signUp'); }}></button>
            </div>
          </div>

        </Route>


        <Route exact path="/newpwd">
          <div className="right-panel">
            <form className="userForm">


              <h2 style={{ padding: "20px 0 0 0" }}> </h2>
              <div className='checkbox'><input type="checkbox" checked={checkState} onClick={() => { checkHandler() }} /><p></p></div>

              <div >      .</div>

              <div className="input-field">
                <i className="fas fa-envelope"></i>
                <input onChange={(e) => { setEmail(e.target.value) }} value={email} type="text" placeholder="  @pukyong.ac.kr" />
              </div>
              <div className="input-field">
                <i className="fas fa-lock"></i>
                <input onChange={(e) => { setStdID(e.target.value) }} value={stdID} type="text" maxLength="9" placeholder="" />
              </div>
              <div className="input-field">
                <i className="fas fa-user"></i>
                <input onChange={(e) => { setName(e.target.value) }} value={name} type="text" placeholder="" />
              </div>
              <div className="submitbox" >
                <button onClick={() => { findPassword() }} type="button" className="SignInBtn"></button>
              </div>
            </form>
            <div className='moveSignPage'>
              <button onClick={() => { reset(); history.push('/') }}></button><button onClick={() => { reset(); history.push('/signUp') }}></button>
            </div>
          </div>
        </Route >
      </Switch >
    </div >



  )
}

export default AccessPage;